<template>
  <div class="mall-wrap">
    <mall-header></mall-header>
    <mall-swipe
      :swipe="swipeList"
    ></mall-swipe>
    <div class="mall-main">
      <mt-swipe :auto="0" :showIndicators="false" style="height: 90px;">
        <mt-swipe-item>
          <div class="nav">
            <router-link to="/mall/goods/vehicle" class="nav-item">
              <i class="fa fa-credit-card" aria-hidden="true" style="color: seagreen"></i>
              买车
            </router-link>
            <a class="nav-item" href="http://usedcar.sih.cq.cn/wx">
              <i class="fa fa-bandcamp" aria-hidden="true" style="color: sienna"></i>
              二手车
            </a>
            <router-link to="/maintain" class="nav-item">
              <i class="fa fa-truck" aria-hidden="true" style="color: cadetblue"></i>
              用车
            </router-link>
            <router-link to="/forum" class="nav-item">
              <i class="fa fa-users" aria-hidden="true" style="color: cornflowerblue"></i>
              社交
            </router-link>
          </div>
        </mt-swipe-item>
        <mt-swipe-item>
          <div class="nav">
            <router-link to="/message" class="nav-item">
              <i aria-hidden="true" class="fa fa-commenting" style="color: steelblue"></i>
              消息
            </router-link>
            <router-link to="/merchant/apply" class="nav-item">
              <i class="fa fa-handshake-o" aria-hidden="true" style="color: brown"></i>
              商户入驻
            </router-link>
            <router-link to="/download" class="nav-item">
              <i class="fa fa-download" aria-hidden="true" style="color: green;"></i>
              下载中心
            </router-link>
          </div>
        </mt-swipe-item>
      </mt-swipe>
      <div class="divider-line"></div>
      <div class="main">
        <mall-rank
          v-for="(item, index) in datas"
          :key="index"
          :rank="item"
        ></mall-rank>
      </div>
    </div>
  </div>
</template>

<script>
import MallHeader from './mall-header.vue'
import MallSwipe from './mall-swipe.vue'
import MallRank from './mall-rank.vue'
import {getSuggest, getIndexSwpieList} from '../../api/mall'
export default {
  name: 'Mall',
  data () {
    return {
      datas: '',
      swipeList: []
    }
  },
  created () {
    this.suggest()
    this.getSwpie()
  },
  methods: {
    suggest () {
      getSuggest()
        .then(data => {
          if (data.state) {
            this.$set(this, 'datas', data.data.rank)
          } else {
            this.$toast(data.errormsg || '获取数据失败')
          }
        })
    },
    getSwpie () {
      getIndexSwpieList()
        .then(data => {
          this.$set(this, 'swipeList', data.data.rank)
        })
    },
    alertText () {
      this.$toast('功能开发中，敬请期待！')
    }
  },
  components: {
    MallHeader,
    MallSwipe,
    MallRank
  }
}
</script>

<style lang="less" scoped>
.mall-wrap{
  width: 100%;
  position: absolute;
  overflow: hidden;
  background-color: #fff;
  &:after{
    display: block;
    width: 100%;
    height: 5rem;
    font-size: .8rem;
    content: '到底部了';
    border-bottom: 1px solid #ddd;
    color: #999;
    text-align: center;
  }
  .mall-main{
    .nav{
      padding: 1rem 0;
      display: flex;
      .nav-item{
        flex: 1;
        font-size: 12px;
        text-align: center;
        color: #333;
        border-right: 1px solid #ddd;
        &:last-child{
          border: none;
        }
        i{
          display: block;
          margin-bottom: .2rem;
          font-size: 2.5rem;
        }
      }
    }
  }
}
</style>



