<template>
  <div class="rank-wrap">
    <div class="title">
      <span>{{rank.category_cn}}</span>
      <span style="float: right;" @click="goToPath(rank.label_id)">
        更多
        <i class="fa fa-angle-right" aria-hidden="true"></i>
      </span>
    </div>
    <div class="clearfix">
      <div class="line" v-for="(item, index) in rank.list" :key="index">
        <mall-item
          v-for="(goods, index) in item"
          :key="index"
          :goods="goods"
        ></mall-item>
      </div>
    </div>
  </div>
</template>

<script>
import MallItem from './mall-item.vue'
export default {
  name: 'Mall-Rank',
  props: {
    rank: ''
  },
  methods: {
    goToPath (id) {
      this.$router.replace('/mall/goods/' + id)
    }
  },
  components: {
    MallItem
  }
}
</script>

<style lang="less" scoped>
  .rank-wrap{
    .title{
      background: #fafafa;
      padding: 6px 15px;
      border-bottom-color: #eee;
      color: #777;
      border-color: #ddd;
      border-bottom: 1px solid transparent;
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
    }
  }
</style>



