<template>
  <div class="header-wrap">
    <mt-search
      v-model="searchData"
      cancel-text="取消"
      placeholder="搜索商品">
    </mt-search>
  </div>
</template>

<script>
export default {
  name: 'Mall-Header',
  data () {
    return {
      searchData: ''
    }
  }
}
</script>

<style lang="less" scoped>
  .header-wrap{
    height: 3rem;
  }
</style>



