<template>
  <div class="swipe-wrap">
    <mt-swipe :auto="4000">
      <mt-swipe-item>
        <img src="/static/img/m-bg1.jpg" alt="">
      </mt-swipe-item>
       <mt-swipe-item>
        <img src="/static/img/m-bg2.jpg" alt="">
      </mt-swipe-item>
    </mt-swipe>
  </div>
</template>

<script>
export default {
  name: 'Mall-Swipe',
  props: {
    swipe: ''
  },
  methods: {
    goTo () {
      window.open('http://wxserver.knowway.cn/Home/zajindan/index?play_id=')
    }
  }
}
</script>

<style lang="less" scoped>
  .swipe-wrap{
    margin-top: 1rem;
    height: 16rem;
    img{
      height: 100%;
      width: 100%;
    }
  }
</style>



